<style scoped>
   a {
    color:#000000;
  }

</style>
<template>
  <div class="uploadalg">

<el-row>
  <el-col :span="6"><div class="grid-content" @click="changenewshow()" style="text-align:left">
      
          <i class="el-icon-circle-plus" v-show="!newshow">
          </i>
          <i class="el-icon-remove" v-show="newshow">
        </i> <a></a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
</el-row>
  <div v-show="newshow">
   <el-row :gutter="20">
    <el-col :span="12">
     <el-row>
       <el-col :span="12">
         
       </el-col>
       <el-col :span="12">
             <el-upload
          class="upload-demo"
          ref="upload"
          action="http://localhost:8081/uploadfile"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :limit="1"
          :on-exceed="handleExceed"
          :on-change="getname"
          :on-success="clearfile"
          :file-list="fileList"
          :auto-upload="false">
          <el-button size="small" type="primary"></el-button>
          </el-upload>
       </el-col>
       <br/>
        <br/> <br/> <br/> <br/> <br/>
      <el-col :span="12">
         
       </el-col>
       <el-col :span="12">

       <el-input
        placeholder=""
        v-model="filename"
        clearable>
        </el-input>

       </el-col>
     </el-row>
    </el-col>
    <el-col :span="12">
         <div class="form-group">
                  <label for="username" class="control-label"></label>
                  <div>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray"> jar
                    </p>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray">flag:10<br/>
                    </p>

                  </div>
                </div>
    </el-col>
         <el-button @click="submitjar" class="btn btn-success" ></el-button>
          <el-button @click='clear' class="btn btn-warning"></el-button>
         <el-button type="info"></el-button>
   </el-row>
  </div>
</div>
</template>
<script>

  export default {
    data() {
      return {
       newshow:false,
      filename: '', //
      jarname:'',
      fileList:[],
      }
    },
    computed: {
   
  },
    created: function () {
      var vm = this;
    

     


    }, 
    methods: {
      changenewshow(){
      var vm = this;
      vm.newshow = !vm.newshow;

       },
       clearfile(){
         this.filename='',
        this.$refs.upload.clearFiles();
       },
       clear: function() {
        var vm = this;
        vm.filename = '';
       this.$refs.upload.clearFiles();
      },
       handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(``);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },
      getname:function(file,fileList){
        var vm=this;
         vm.filename=file.name;
         vm.jarname=file.name;
      },
       upload: function(id) {
        var vm = this;
        if (vm.filename=='' || vm.jarname == '') {
          alert("");
          return;
        }
      this.$refs.upload.submit();
      },
      submitjar(){
           var vm = this;
        var url = "http://localhost:8081/jarfile/addone";
  
           $.ajax({
          url: url,
          type: "POST",
          async:false,
          dataType: 'json',
          data: {
           "jarfilename":vm.jarname,
           "jarname":vm.filename,
          },
           success: function (data) {
            //data jsonjson
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
              if(data.code==0){
            vm.upload();
             vm.$notify({
             title: '',
             message: '',
             type: 'success'
             });}
            else{
              vm.$notify.error({
               title: '',
              message: data.msg
            });
            }
            vm.clear();
            vm.fresh();
          },
           error: function (data) {
            vm.$notify.error({
               title: '',
              message: data.msg
            })
            console.log(data);
          }
        });
      },
       fresh() {
      this.$emit('fresh'); //upup'hehe'
    },
    }
  }
</script>



